/* ==========================================================================
   Optional configurations
   ========================================================================== */

/**
 * Headings
 */

h1,
h2,
h3,
h4,
h5,
h6 {
  @include optional-prop("color", $initialize-heading-color);
  @include optional-prop("margin", $initialize-heading-margins);
  @include optional-prop("font-family", $initialize-heading-font-family);
}

/**
 * Block elements
 */

p,
table,
ul,
ol,
fieldset,
blockquote,
dl {
  @include optional-prop("margin", $initialize-block-margins);
}

/* ==========================================================================
   Print styles
   ========================================================================== */

@if $initialize-print-styles == true {
  @media print {
    *,
    *:before,
    *:after,
    *:first-letter,
    *:first-line {
      background: transparent !important;
      color: #000 !important; // Black prints faster: http://www.sanbeiji.com/archives/953
      box-shadow: none !important;
      text-shadow: none !important;
    }

    a,
    a:visited {
      text-decoration: underline;
    }

    a[href]:after {
      content: " (" attr(href) ")";
    }

    abbr[title]:after {
      content: " (" attr(title) ")";
    }

    // Don't show links that are fragment identifiers,
    // or use the `javascript:` pseudo protocol
    a[href^="#"]:after,
    a[href^="javascript:"]:after {
      content: "";
    }

    pre,
    blockquote {
      border: 1px solid #999;
      page-break-inside: avoid;
    }

    // Printing Tables:
    // http://css-discuss.incutio.com/wiki/Printing_Tables
    thead {
      display: table-header-group;
    }

    tr,
    img {
      page-break-inside: avoid;
    }

    img {
      max-width: 100% !important;
    }

    p,
    h2,
    h3 {
      orphans: 3;
      widows: 3;
    }

    h2,
    h3 {
      page-break-after: avoid;
    }
  }
}
